import React, { Component } from 'react';
import {
  IonCard,
  IonCardHeader,
  IonCardTitle,
  IonCardContent,
  IonButton,
  IonIcon
} from '@ionic/react';
import IDulces from '../models/IDulces';
import { trash } from 'ionicons/icons/index';

interface IPropsDulcesCard extends IDulces {
  funcionElimina: ((event: React.MouseEvent<HTMLIonButtonElement, MouseEvent>) => void) | undefined;
}

export default class DulceCard extends Component<IPropsDulcesCard, IDulces> {
  render() {
    return (
      <IonCard>
        <IonCardHeader>
          <IonCardTitle> {this.props.producto} </IonCardTitle>
        </IonCardHeader>
        <IonCardContent>
          <h5>Cantidad: {this.props.cantidad} </h5>
          <h6>Precio por unidad: ${this.props.precio} </h6>
          <IonButton color="danger" onClick={this.props.funcionElimina}>
            <IonIcon src={trash}></IonIcon>
          </IonButton>
        </IonCardContent>
      </IonCard>
    );
  }
}