import React, { Component } from 'react';
import {
  IonApp,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonList,
  IonItem,
  IonAlert,
  AlertInput,
  AlertButton,
  IonIcon,
  IonFab,
  IonFabButton,
  IonFabList
} from '@ionic/react';
import IDulces from './models/IDulces';
import '@ionic/react/css/core.css';
import DulceCard from './components/DulcesCard';
import {
  arrowUpCircle,
  addCircle,
  removeCircle,
  calculator
} from 'ionicons/icons'

/**
 * Interfaz para definir el estado de App.
 */
interface AppState {
  listaDeDulces: IDulces[];
  mostrarMAñadir: boolean;
  mostrarMEliminarLista: boolean;
  mostrarTotal: boolean;
  mostrarMEliminarDulces: boolean;
  elementoAEliminar: IDulces | null;
}

/**
 * Componente de React donde estara la interfaz principal de la 
 * aplicación.
 */
export default class App extends Component {
  state: AppState = {
    listaDeDulces: [],
    mostrarMAñadir: false,
    mostrarMEliminarLista: false,
    mostrarTotal: false,
    mostrarMEliminarDulces: false,
    elementoAEliminar: null
  };

  private entradaProducto: AlertInput = {
    name: 'producto',
    type: 'text',
    id: 'producto',
    placeholder: 'Producto'
  };

  private entradaCantidad: AlertInput = {
    name: 'cantidad',
    type: 'number',
    id: 'cantidad',
    placeholder: 'Cantidad'
  };

  private entradaPrecio: AlertInput = {
    name: 'precio',
    type: 'number',
    id: 'precio',
    placeholder: 'Precio por pieza'
  };

  private botonCancelar: AlertButton = {
    text: 'Cancel',
    role: 'cancel',
    cssClass: 'danger'
  };

  private añadirDulces = () => this.setState({ mostrarMAñadir: true });

  private eliminarLista = () => this.setState({ mostrarMEliminarLista: true });

  private calcularTotal = () => this.setState({ mostrarTotal: true });

  private elimirDulces = (e: IDulces) => this.setState({
    mostrarMEliminarDulces: true,
    elementoAEliminar: e
  });

  private total = () => {
    let total: number = 0;
    this.state.listaDeDulces.map(i => total += (i.precio * i.cantidad));
    return total;
  };

  render() {
    return (
      <IonApp>
        <IonHeader>
          <IonToolbar>
            <IonTitle>Caja</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent>
          <IonList>
            {this.state.listaDeDulces.map(i => (
              <IonItem>
                <DulceCard producto={i.producto}
                  cantidad={i.cantidad}
                  precio={i.precio}
                  funcionElimina={() => {
                    this.elimirDulces(i);
                  }} />
              </IonItem>
            ))}
          </IonList>

          <IonFab vertical="bottom" horizontal="end">
            <IonFabButton>
              <IonIcon icon={arrowUpCircle} />
            </IonFabButton>
            <IonFabList side="top">
              <IonFabButton color="blue" onClick={this.calcularTotal}>
                <IonIcon icon={calculator} />
              </IonFabButton>
              <IonFabButton color="danger" onClick={this.eliminarLista}>
                <IonIcon icon={removeCircle} />
              </IonFabButton>
              <IonFabButton color="primary" onClick={this.añadirDulces}>
                <IonIcon icon={addCircle} />
              </IonFabButton>
            </IonFabList>
          </IonFab>
        </IonContent>

        <IonAlert isOpen={this.state.mostrarMAñadir}
          onDidDismiss={() => this.setState({ mostrarMAñadir: false })}
          header="Añadir"
          inputs={[
            this.entradaProducto,
            this.entradaCantidad,
            this.entradaPrecio
          ]}
          buttons={[
            this.botonCancelar,
            {
              text: 'Ok',
              handler: data => {
                const { cantidad, precio, producto } = data;
                if (cantidad && precio && producto && producto !== ''
                    && cantidad >= 0 && precio >= 0) {
                  let nuevosDulces: IDulces = {
                    cantidad, precio, producto
                  };
                  this.setState((ps: AppState) => {
                    return {
                      listaDeDulces: [...ps.listaDeDulces, nuevosDulces]
                    }
                  });
                }
              }
            }
          ]} />

        <IonAlert isOpen={this.state.mostrarMEliminarLista}
          onDidDismiss={() => this.setState(
            { mostrarMEliminarLista: false })}
          header="Eliminar lista de dulces"
          message="¿Seguro que quiere eliminar la lista?"
          buttons={[
            this.botonCancelar,
            {
              text: 'Ok',
              handler: () => this.setState({ listaDeDulces: [] })
            }
          ]} />

        <IonAlert isOpen={this.state.mostrarTotal}
          onDidDismiss={() => this.setState({ mostrarTotal: false })}
          header="Total:"
          message={"<h4>$" + this.total().toString() + "</h4>"}
          buttons={[
            {
              text: 'OK'
            }
          ]} />
        <IonAlert isOpen={this.state.mostrarMEliminarDulces}
          onDidDismiss={() => this.setState({ mostrarMEliminarDulces: false })}
          header="Eliminar Dulces"
          message={"¿Seguro que quiere eliminar estos dulces?"}
          buttons={[
            this.botonCancelar,
            {
              text: 'Ok',
              handler: () => this.setState((ps: AppState) => {
                return {
                  listaDeDulces: ps.listaDeDulces.filter(
                    e => e !== ps.elementoAEliminar
                  ),
                  elementoAEliminar: null
                }
              })
            }
          ]} />
      </IonApp>
    );
  }
}